.default-quote{
    max-width: 100%;
    height: auto;
}

.component-div{
    position: absolute;
    top: 10px;
    /* left: 20px; */
    width: 100%;
    margin: auto;
}

.text-div{
    display: flex;
    /* border: 1px solid red; */
    width: 100%;
    position: absolute;
    bottom: 2px;
    /* padding: ; */
}

#profile{
    position: relative;
    left: 190px;
    background-color: rgb(154, 247, 247);
    color: black;
    padding: 5px;
    width: 27%;
    margin: auto;
}
#icons-div{
    font-size: 20px;
    color: rgba(255, 0, 0, 0.897);
    font-weight: bold;
    position: relative;
    right: 10px;
}



.field-select select{
    width: 20%;
    padding: 7px;
    border-radius: 12px;
    font-size: 15px;
    outline: none;
}

#btnQuote button{
    position: relative;
    left: 10px;
    padding: 5px;
    border-radius: 5px;
    font-size: 17px;
    color: #fff;
    background-color: #28a745;
    border-color: #28a745;
    outline: none;
}

#btnQuote button:hover{
    background-color: #218838;
    border: 1px solid #218838;
}